/* Portfolio.css */

.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .box {
    box-sizing: border-box;
    width: 48%; /* Adjust the width as needed for larger screens */
    margin-bottom: 2rem;
    padding: 1rem;
  }

  .image-container {
    max-width: 40%;
    margin: 0 auto; /* Center the image container */
  }
  
  /* Media Query for Tablets */
  @media screen and (max-width: 768px) {
    .box {
      width: 100%;
    }
  }
  